import Vue from 'vue'
import $ from 'jquery'
import ElementUI from 'element-ui'
import VueResource from 'vue-resource'
import App from './App.vue'
import store from './store'
import './script/axiosExt'
import 'element-ui/lib/theme-chalk/index.css'
import 'font-awesome/css/font-awesome.css'

import './styles/globalStyle.css'
import mixinForClient from './script/mixin'

import IntroJs from 'intro.js'
import 'intro.js/introjs.css'

Vue.use(ElementUI)
Vue.use(VueResource)
Vue.use(IntroJs);

Vue.config.productionTip = false

Vue.prototype.$sleep = function (time) {
    var startTime = new Date().getTime() + parseInt(time, 10);
    while (new Date().getTime() < startTime) { }
}

new Vue({
    store,
    mixins: [mixinForClient],
    render: h => h(App),
}).$mount('#app')

$(function () {
    function modifyCss() {
        $(".flex").css({ height: window.innerHeight - 16 + 'px' });
    };
    modifyCss();
    window.addEventListener("resize", modifyCss, false);
});



