function sleep(milliSeconds) {
    var startTime = new Date().getTime();
    while (new Date().getTime() < startTime + milliSeconds) {
    }
}

function compareArray(obj1, obj2, compareCallback) {
    var isSame = false;
    if (Object.prototype.toString.call(obj1) == '[object Array]' && Object.prototype.toString.call(obj2) == '[object Array]') {
        if (obj1.length != obj2.length) {
            isSame = false;
        }
        else if (obj1.length == 0) {
            isSame = true;
        }
        else {
            // 取key集合，比较
            if (Object.keys(obj1[0]).join(',') != Object.keys(obj2[0]).join(',')) {
                isSame = false;
            }
            else {
                var keys = Object.keys(obj1[0]);
                var i = 0;
                for (; i < obj1.length; ++i) {
                    var j = 0;
                    for (; j < obj2.length; ++j) {
                        if (undefined != compareCallback && null != compareCallback) {
                            if (!compareCallback(obj1[i], obj2[j])) {
                                break;
                            }
                        }
                        else {
                            var k = 0;
                            for (; k < keys.length; k++) {
                                if (obj1[i][keys[k]] != obj2[j][keys[k]]) {
                                    break;
                                }
                            }

                            if (k == keys.length) { //找到一个相同的对象
                                break;
                            }
                        }
                    }

                    if (j == obj2.length) { //在obj2里面没有找到与obj1相同的对象
                        isSame = false;
                        break;
                    }
                }

                if (i == obj1.length) { //obj1 == obj2
                    isSame = true;
                }
            }
        }
    }

    return isSame;
}

function parseServerApiAddress(serApiAddr) {
    var val = new Object();
    var serApiDataArr = serApiAddr.toString().split("&");
    for (var i = 0; i < serApiDataArr.length; ++i) {
        var serApiData = serApiDataArr[i].toString().split("=");
        if (2 == serApiData.length) {
            val[serApiData[0].toString()] = serApiData[1].toString();
        }
    }
    if (val.hasOwnProperty('islogin') && val.hasOwnProperty('tokenid') && '' != val.tokenid) {
        val.islogin = ('true' == val.islogin ? true : false);
    }
    else {
        val.islogin = false;
    }
    return val;
}

function parseSoftwareList(dataContext, tokenid, aestokenid, productCfg) {
    //处理tokenid为非真实的情况
    if (/^\d{10,25}$/g.test(tokenid)) {
        tokenid = undefined;
        aestokenid = undefined;
    }

    var userCfg = JSON.parse(sessionStorage.getItem("userCfg"));
    var appData = JSON.parse(sessionStorage.getItem("serverMap")).appdata;
    var moduleTemplate = JSON.parse(sessionStorage.getItem('moduleParamsTemplate'));
    if (undefined == moduleTemplate || null == moduleTemplate) {
        return [];
    }
    // var softwareProducts = JSON.parse(sessionStorage.getItem("softwareProducts"));
    var serApiAddr = JSON.parse(sessionStorage.getItem("serApiAddr"));

    let index = 0;
    var softList = new Array;
    var curTime = Date.now();
    if (undefined != dataContext && null != dataContext && dataContext.length > 0) {
        for (var i = 0; i < dataContext.length; ++i) {
            if ((undefined != appData.hidden && -1 != appData.hidden.indexOf(dataContext[i].Id)) &&
                (undefined == userCfg.ShowModules || -1 == userCfg.ShowModules.indexOf(dataContext[i].Id))) {
                continue;
            }

            var moduleData = new Object();
            moduleData.runType = 6;
            moduleData.collection = (undefined != dataContext[i].IsCollection && null != dataContext[i].IsCollection) ? dataContext[i].IsCollection : false;
            moduleData.status = 0;
            moduleData.icon = "./Img/icons/1.jpg";
            moduleData.img = "./Img/img-default.gif";
            moduleData.time = dataContext[i].CreateDate.substring(0, 10);
            moduleData.name = dataContext[i].Name;
            moduleData.id = dataContext[i].Id;
            moduleData.free = dataContext[i].IsFree ? 1 : 0
            if (undefined != dataContext[i].Values && dataContext[i].Values.length > 0) {
                for (var j = 0; j < dataContext[i].Values.length; ++j) {
                    if (moduleTemplate.Name.fieldid == dataContext[i].Values[j].key) {
                        moduleData.name = dataContext[i].Values[j].value;
                        if (false) {
                            if (undefined != softwareProducts && undefined != serApiAddr) {
                                let productId = undefined;
                                for (var k = 0; k < softwareProducts.length; ++k) {
                                    if (undefined != softwareProducts[k].ModuleIds) {
                                        if (-1 != softwareProducts[k].ModuleIds.indexOf(moduleData.id)) {
                                            productId = softwareProducts[k].Id;
                                            break;
                                        }
                                    }
                                }
                                moduleData.pUrl = JSON.parse(sessionStorage.getItem("serverMap")).leadingwebserver;
                                moduleData.mUrl = JSON.parse(sessionStorage.getItem("serverMap")).leadingwebserver;
                                if (serApiAddr.islogin) {
                                    moduleData.pUrl += "/view/TRPath/T/Mall/" + serApiAddr.userid + "/Product/" + productId;
                                    moduleData.mUrl += "/view/TRPath/T/Mall/" + serApiAddr.userid + "/Product/" + productId + "/MallDetail/" + moduleData.id;
                                } else {
                                    moduleData.pUrl += "/view/TRPath/T/Mall/1/Product/" + productId;
                                    moduleData.mUrl += "/view/TRPath/T/Mall/1/Product/" + productId + "/MallDetail/" + moduleData.id;
                                }
                            }
                        } else {
                            moduleData.pUrl = JSON.parse(sessionStorage.getItem("serverMap")).leadingwebserver
                                + "/T/CADMallProduct/"
                                + dataContext[i].Id
                                + ".html&tokenid="
                                + aestokenid;
                            moduleData.mUrl = JSON.parse(sessionStorage.getItem("serverMap")).leadingwebserver
                                + "/T/CADMallModule/"
                                + dataContext[i].Id
                                + ".html&tokenid="
                                + aestokenid;
                        }
                    } else if (moduleTemplate.Extension.fieldid == dataContext[i].Values[j].key) {
                        moduleData.fileExtName = dataContext[i].Values[j].value;
                    } else if (moduleTemplate.ExecutionPath.fieldid == dataContext[i].Values[j].key) {
                        moduleData.app = dataContext[i].Values[j].value;
                    } else if (moduleTemplate.Image.fieldid == dataContext[i].Values[j].key) {
                        if (dataContext[i].Values[j].value.length > 0) {
                            if (undefined != dataContext[i].Values[j].value[0].fileid) {
                                moduleData.img = JSON.parse(sessionStorage.getItem("serverMap")).leadingfileserver + "/api/fileserver/gallerythumbnailimg/"
                                    + dataContext[i].Values[j].value[0].fileid + '/'
                                    + tokenid;
                            }
                        }
                    }
                }
            }
            if (dataContext[i].hasOwnProperty('BrowseNum')) {
                moduleData.BrowseNum = dataContext[i].BrowseNum;
            }
            if (dataContext[i].hasOwnProperty('CreateDate')) {
                moduleData.CreateDate = dataContext[i].CreateDate;
            }
            if (dataContext[i].hasOwnProperty('UpdateDate')) {
                moduleData.UpdateDate = dataContext[i].UpdateDate;
            }
            if (dataContext[i].hasOwnProperty('usedTimes')) {
                moduleData.usedTimes = dataContext[i].usedTimes;
            }
            if (dataContext[i].hasOwnProperty('CollectionNum')) {
                moduleData.CollectionNum = dataContext[i].CollectionNum;
            }
            if (dataContext[i].hasOwnProperty('LatestCalculateDate')) {
                moduleData.LatestCalculateDate = dataContext[i].LatestCalculateDate;
            }
            if (dataContext[i].hasOwnProperty('BriefDescription')) {
                moduleData.BriefDescription = dataContext[i].BriefDescription;
            }
            moduleData.FileMgrType = 0;
            if (typeof moduleData.app == 'string') {
                var productName = '';
                try {
                    var obj = JSON.parse(moduleData.app);
                    if (undefined != obj && null != obj) {
                        if (typeof obj == 'object') {
                            moduleData.FileMgrType = 1;
                            if (obj.hasOwnProperty('Product')) {
                                productName = obj.Product;
                            }
                            else if (obj.hasOwnProperty('ExePath')) {
                                if (-1 != obj.ExePath.indexOf('{{AppPath}}')) {
                                    productName = obj.ExePath.substring(obj.ExePath.indexOf('{{AppPath}}') + 11);
                                }
                                if (-1 != productName.indexOf('\\')) {
                                    productName = productName.substring(0, productName.indexOf('\\'));
                                }
                                else {
                                    productName = '';
                                }
                            }
                            else if (obj.hasOwnProperty('ExeFile')) {
                                if (-1 != obj.ExeFile.indexOf('{{AppPath}}')) {
                                    productName = obj.ExeFile.substring(obj.ExeFile.indexOf('{{AppPath}}') + 11);
                                }
                                if (-1 != productName.indexOf('\\')) {
                                    productName = productName.substring(0, productName.indexOf('\\'));
                                }
                                else {
                                    productName = '';
                                }
                            }
                            else if (obj.hasOwnProperty('ModuleFile')) {
                                if (-1 != obj.ModuleFile.indexOf('{{AppPath}}')) {
                                    productName = obj.ModuleFile.substring(obj.ModuleFile.indexOf('{{AppPath}}') + 11);
                                }
                                if (-1 != productName.indexOf('\\')) {
                                    productName = productName.substring(0, productName.indexOf('\\'));
                                }
                                else {
                                    productName = '';
                                }
                            }
                            else {
                                productName = '';
                            }
                        }
                        else {

                        }
                    }
                }
                catch (e) {
                    if (-1 != moduleData.app.indexOf('\\')) {
                        productName = moduleData.app.substring(0, moduleData.app.indexOf('\\'));
                    }
                    else {
                        productName = '';
                    }
                }
                moduleData.Product = productName;
                moduleData.Installed = false;
                moduleData.curKey = (++curTime).toString();
                if (undefined != productCfg && null != productCfg && Object.prototype.toString.call(productCfg) == '[object Array]') {
                    moduleData.Installed = (0 == productCfg.length ? true : false); // 兼容旧程序
                    for (var Indexcfg = 0; Indexcfg < productCfg.length; ++Indexcfg) {
                        if (productName.toLowerCase() == productCfg[Indexcfg].Product.toLowerCase()) {
                            moduleData.Product = productCfg[Indexcfg].Product;
                            moduleData.ProductPath = productCfg[Indexcfg].ProductPath;
                            moduleData.Installed = productCfg[Indexcfg].Installed;
                            break;
                        }
                    }
                }
            }

            moduleData.purchase = 1;
            moduleData.use = 1;
            if (undefined != appData && null != appData) {
                if (undefined != appData.disablepurchase && null != appData.disablepurchase && Object.prototype.toString.call(appData.disablepurchase) == '[object Array]') {
                    if (-1 != appData.disablepurchase.indexOf(moduleData.id)) {
                        moduleData.purchase = 0;
                    }
                }

                if (undefined != appData.disableuse && null != appData.disableuse && Object.prototype.toString.call(appData.disableuse) == '[object Array]') {
                    if (-1 != appData.disableuse.indexOf(moduleData.id)) {
                        moduleData.use = 0;
                    }
                }
            }
            softList[index++] = moduleData;
        }
    }
    return softList;
}

function localhostFunc(requestPar, persistentPar, successCallback, failureCallback) {
    if (undefined != window.NativeQuery) {
        window.NativeQuery({
            request: requestPar,
            persistent: persistentPar,
            onSuccess: function (response) {
                if (undefined != successCallback && null != successCallback) {
                    successCallback(response);
                }
            },
            onFailure: function (code, msg) {
                if (undefined != failureCallback && null != failureCallback) {
                    failureCallback(code, msg);
                }
                else {
                    console.log(code + '-' + msg);
                }
            }
        });
    } else {
        console.log("undefined window.NativeQuery");
    }
}

function getAuth(successCallback, failureCallback) {
    localhostFunc(
        '{ "LocalFunc": { "FuncId": "getAuth"} }',
        false,
        function (response) {
            if (undefined != successCallback && null != successCallback) {
                var serApiAddr = parseServerApiAddress(response.toString());
                sessionStorage.setItem('serApiAddr', JSON.stringify(serApiAddr));
                successCallback(serApiAddr);
            }
        },
        function (code, msg) {
            if (undefined != failureCallback && null != failureCallback) {
                failureCallback(code, msg);
            }
            else {
                console.log(code + '-' + msg);
            }
        }
    );
}

