function topThree(tableData) {
    var arr = new Array();

    tableData.forEach(element => {
        if (element.icon != undefined) {
            arr.push(element);
        }
    });
    return arr;
}

function excludeTopThree(tableData) {
    var arr = new Array();
    tableData.forEach(element => {
        if (element.icon == undefined) {
            arr.push(element);
        }
    });
    return arr;
}

