//导航菜单路由href统一处理(objNavMenu-菜单数据对象 详情参看app.js)
//【仅供App.vue和navMenu.vue使用】
function handleRoutHref(objNavMenu) {
    var href = "";
    if (undefined != objNavMenu.href) {
        href = objNavMenu.href;
        if ("精品推荐" != objNavMenu.Name && "操作指引" != objNavMenu.Name && "已购买" != objNavMenu.Name &&
            "常用" != objNavMenu.Name && "最近" != objNavMenu.Name && "收藏" != objNavMenu.Name &&
            "我的" != objNavMenu.Name && "全部软件" != objNavMenu.Name) {
            href += "-" + objNavMenu.key;
        }
    }
    return href;
}
