// 取模块列表
function getModuleList(filterPar, tokenid, aestokenid, islogin, filterCallback, collectionCallback, authModuleIdCallback, authCallcack) {
    var urlPlat = JSON.parse(sessionStorage.getItem('serverMap')).cadsvrplatserver;
    if (false) {
    //if ((!islogin || undefined == tokenid || null == tokenid || "" == tokenid)) {
        var devAppIdSecret = JSON.parse(sessionStorage.getItem("serverMap")).DevAppIdSecret;
        urlAddr = JSON.parse(sessionStorage.getItem("serverMap")).leadingapiserver + "/api/authorize/getappaccesstoken/" + devAppIdSecret;
        var tokenid_;
        axios.get(urlAddr).then(function (res) {
            if (undefined != res.data.DataContext) {
                tokenid_ = res.data.DataContext.access_token;
                urlPlat += "/devapi/CADSvrPlat/";
                urlAddr = urlPlat + "/Module/GetList/123456/" + tokenid_;

                return axios.post(urlAddr, qs.stringify(filterPar));
            }
        }).then(function (res) {
            // 响应成功回调
            if (undefined != res.data.DataContext) {
                if (undefined != filterCallback && null != filterCallback) {
                    filterCallback(res.data.DataContext, tokenid_, null);
                }
            }
        }).catch(res => {
            console.log(res);
        })
    } else {
        var urlAddr = urlPlat + '/api/CADSvrPlat/Module/GetListN/' + tokenid;
        axios.post(urlAddr, qs.stringify(filterPar)).then(function (res) {
            // 响应成功回调
            if (undefined != res.data.DataContext) {
                if (undefined != filterCallback && null != filterCallback) {
                    filterCallback(res.data.DataContext, tokenid, aestokenid);
                }

                if (islogin) {
                    // 获取模块的收藏状态 {{UrlPlat}}/UserCollection/GetCollectionList/0/{{TokenId}}
                    urlAddr = urlPlat + '/api/CADSvrPlat/UserCollection/GetCollectionList/0/' + tokenid;
                    var paramDataForCollect = {
                        StartIndex: 0,
                        Limit: filterPar.Limit,
                        WhereCondition: [
                            {
                                FieldCondition: [
                                    {
                                        Name: "CollectionState",
                                        QueryType: 20,
                                        Value: true,
                                        DataType: 20
                                    }
                                ]
                            },
                            {
                                FieldCondition: [
                                    {
                                        Name: "Type",
                                        QueryType: 20,
                                        Value: 0,
                                        DataType: 30
                                    }
                                ]
                            }
                        ],
                        IsGetUsers: true
                    };
                    return axios.post(urlAddr, qs.stringify(paramDataForCollect));
                }
            }
        }).then(res => {
            if (islogin) {
                if (undefined != res.data.DataContext && null != res.data.DataContext && undefined != res.data.DataContext.Record && null != res.data.DataContext.Record) {
                    if (undefined != collectionCallback && null != collectionCallback) {
                        collectionCallback(res.data.DataContext.Record);
                    }
                }

                if (undefined != authModuleIdCallback && null != authModuleIdCallback) {
                    // 根据模块id请求模块是否可用api
                    var urlPlat_ = JSON.parse(sessionStorage.getItem('serverMap')).cadcloudsoftwaremallserver;
                    urlPlat_ += '/api/CADSoftwareMall/Authorization/CheckAuthorization/' + tokenid;
                    // 批量获取模块授权状态
                    var arrModuleId = authModuleIdCallback();
                    var paramData = {
                        "": arrModuleId
                    };
                    if (undefined != arrModuleId && null != arrModuleId && arrModuleId.length > 0) {
                        axios.post(urlPlat_, qs.stringify(paramData, { arrayFormat: 'repeat' })).then(res => {
                            if (undefined != res.data.DataContext && res.data.DataContext.length > 0) {
                                if (undefined != authCallcack && null != authCallcack) {
                                    authCallcack(res.data.DataContext);
                                }
                            }
                        }).catch(res => {
                            if (!axios.isCancel(res)) {
                                console.log(res);
                            }
                        });
                    }
                }
            }
        }).catch(res => {
            console.log(res);
        });
    }
}

