import { handleRoutHref } from './common'
export default {
	name: 'navMenu',
	props: ['navMenus', 'curIndex', 'menus'],
	data() {
		return {}
	},
	mounted: function () {
		// console.log("NavMenu mounted: function");
	},
	watch: {
		$route(to, from) {
			var navMenu = new Object();
			navMenu.name = "全部软件";
			navMenu.key = '';
			if (to.fullPath.indexOf('purchased') > 0) {
				navMenu.Name = "已购买";
				sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
			} else if (to.fullPath.indexOf('commonlyused') > 0) {
				navMenu.Name = "常用";
				sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
			} else if (to.fullPath.indexOf('lately') > 0) {
				navMenu.Name = "最近";
				sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
			} else if (to.fullPath.indexOf('collection') > 0) {
				navMenu.Name = "收藏";
				sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
			} else if (to.fullPath == '/') {
				navMenu.Name = "精品推荐";
				sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
			} else if (to.fullPath == '/software/all') {
				var objCurMenu = JSON.parse(sessionStorage.getItem('curNavMenu'));
				if (undefined == objCurMenu) {
					navMenu.Name = "全部软件";
					sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
				}
			} else {
				var objCurSelMenu = JSON.parse(sessionStorage.getItem('curNavMenu'));
				if (undefined != objCurSelMenu && null != objCurSelMenu) { //【点击后】
					//如果导航菜单名是：已购买，常用，最近，收藏， 则设置key为空字符串
					if (objCurSelMenu.Name == "已购买" || objCurSelMenu.Name == "常用" ||
						objCurSelMenu.Name == "最近" || objCurSelMenu.Name == "收藏") {
						navMenu.Name = objCurSelMenu.Name; //临时增加菜单名字用于区分路由刷新时无Name不能请求数据。
						sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
					}
				}
			}
		}
	},
	methods: {
		getCurrentIndex: function (navMenu) {
			return handleRoutHref(navMenu);
		},
		showNavMenu: function (navMenu) {
			return (undefined != navMenu.Items && navMenu.Items.length > 0) && (undefined == navMenu.showItems || navMenu.showItems);
		},
		clickItem: function (navMenu, event) {
			// 当前选择菜单
			sessionStorage.setItem('curNavMenu', JSON.stringify(navMenu));
			// 路由
			if (undefined != navMenu.href) {
				var routerPath = handleRoutHref(navMenu);
				this.$router.replace({
					path: routerPath
				}).catch(() => { });
			}
		}
	}
}
