import store from '../store'
import axios from 'axios'
import { getAuth, parseServerApiAddress } from './common';
// import { Loading } from 'element-ui'
// import $ from 'jquery'

let loadingInstance;
//定义一个混入对象
var mixinForClient = {
    mounted: function () {
        window.myVue = this;
    },

    methods: {
        //发送更新数据消息【客户端使用】
        UpdateData: function (defRoutePath = '/') {
            store.dispatch('loading', { isChange: true })
            let that = this;
            getAuth(
                function (serApiAddr) {
                    //没有登录,调用客户端登录
                    if (!serApiAddr.islogin) {
                        var curNavMenu = JSON.parse(sessionStorage.getItem('curNavMenu'));
                        if (curNavMenu && (curNavMenu.Name == "我的" || curNavMenu.Name == "已购买" || curNavMenu.Name == "收藏" ||
                            curNavMenu.Name == "常用" || curNavMenu.Name == "最近")) {
                            that.$router.push(defRoutePath);
                        }
                    }
                    else {
                        store.dispatch('loading', { isChange: true })
                    }
                },
                function (code, msg) {
                    console.log(code + msg);
                }
            );
        },

        //路由跳转【客户端使用】
        RoutePush: function (routePath) {
            if (routePath && Object.prototype.toString.call(routePath) == '[object String]') {
                this.$router.push(routePath);
            }
        },

        startLoading() {
            loadingInstance = this.$loading({
                lock: true,
                text: 'loading',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0)',
                target: document.querySelector('.software_list_.software-list-height')
            })
        },

        endLoading() {
            loadingInstance.close();
        }
    }
}

export default mixinForClient