import navmenu from '../components/navMenu.vue'
import router from '../router/router.js'
import { handleRoutHref, getAuth, getServer, getUserCfg, setUserCfg } from './common.js'
import bus from '../assets/eventBus'
import { mapState } from 'vuex'
import store from '../store'
// import { mainAppIntro } from "../utils/intro.js";

export default {
	name: 'app',
	router,
	components: {
		navmenu
	},
	props: {},
	data() {
		return {
			Items: [],
			serverMap: Object,
			moduleParamsTemplate: Object,

			navMenuOpends: [],
			isLogin: false,

			showQuickStartTutorial: true
		}
	},
	watch: {
		isLogin(newVal, oldVal) {
			if (newVal != oldVal) {
				this.getModuleTemplate();
			} else {
				store.dispatch('loading', { isChange: false })
			}
		},
		loginChange(newVal, oldVal) {
			if (newVal != oldVal && newVal) {
				let that = this;
				getAuth(
					function (serApiAddr) {
						that.isLogin = serApiAddr.islogin;
					},
					function (code, msg) {
						console.log(code + msg);
					}
				);
			}
		},
		showQuickStartTutorial(newVal, oldVal) {
			if (newVal != oldVal) {
				this.getModuleTemplate();
			} else {
				store.dispatch('loading', { isChange: false })
			}
		},
		showQuickStartTutorialChange(newVal, oldVal) {
			if (newVal != oldVal) {
				this.showQuickStartTutorial = newVal;
			}
		}
	},
	computed: {
		...mapState([
			'loginChange',
			'showQuickStartTutorialChange'
		])
	},
	created: function () {
		this.initData();

		bus.$on('template', (msg) => {
			if (1 == msg) {
				if (!store.state.loginChange) {
					this.navMenuOpends.length = 0;
					//递归 获取默认展开几层菜单（arrIn-菜单数据，类型是数组 numFilter-展开的层数 arrOut-出参，展开的菜单索引数组）
					(function getDefOpends(arrIn, numFilter, arrOut) {
						if (numFilter == 0) {
							return;
						}

						if (arrIn instanceof Array) {
							--numFilter;
							for (var i = 0; i < arrIn.length; i++) {
								if (arrIn[i].hasOwnProperty('Items') && arrIn[i].Items.length > 0) {
									arrOut.push(handleRoutHref(arrIn[i]));
									getDefOpends(arrIn[i].Items, numFilter, arrOut);
								}
							}
						} else {
							return;
						}

					})(this.Items, 1, this.navMenuOpends);
					// console.log(this.navMenuOpends);
				}

			}
		});
	},
	mounted: function () {
		window.myVue = this;
		// var showIntroInfo = JSON.parse(sessionStorage.getItem('ShowIntroInfo'));
		// if (undefined != showIntroInfo && showIntroInfo) {
		// 	mainAppIntro.start().oncomplete(async ()=>{
		// 		await this.$store.dispatch('setIntro', {mainAppIntro:false})
		// 	  });
		// }
	},
	methods: {
		initData() {
			//this.getLocalData();
			this.genServerMap();

			let that = this;

			getUserCfg(
				function (res) {
					var userCfg = JSON.parse(res);
					sessionStorage.setItem('userCfg', JSON.stringify(userCfg));
					if (undefined == userCfg.ShowIntroInfo || userCfg.ShowIntroInfo) {
						sessionStorage.setItem('ShowIntroInfo', JSON.stringify(true));
						userCfg.ShowIntroInfo = false;
						setUserCfg(
							JSON.stringify(userCfg),
							function (res) {

							},
							function (code, msg) {
								console.log(code + '-' + msg);
							}
						);
					}
				},
				function (code, msg) {
					console.log(code + '-' + msg);
				}
			);
			
			getAuth(
				function (serApiAddr) {
					that.isLogin = serApiAddr.islogin;
					// that.getSoftwareProducts(serApiAddr);
					that.getModuleTemplate();
				},
				function (code, msg) {
					console.log(code + '-' + msg);
				}
			);
		},

		getLocalData() {
			this.$http.get('./localData.json', {}, {
				emulateJSON: true
			}).then(function (res) {
				if (undefined != res.data.localData) {
					sessionStorage.setItem('localData', JSON.stringify(res.data.localData));
				}
				if (undefined != res.data.serverMap) {
					sessionStorage.setItem('serverMap', JSON.stringify(res.data.serverMap));
				}
				sessionStorage.setItem('loadLocalData', undefined == res.data.loadLocalData ? 'false' : JSON.stringify(res.data.loadLocalData));
			}, function (res) {
				// 响应错误回调
				console.log(res);
			})
		},

		genServerMap() {
			let that = this;
			var loadLocalData = JSON.parse(sessionStorage.getItem('loadLocalData'));
			if (undefined != window.NativeQuery && (undefined == loadLocalData || !loadLocalData)) {
				getServer(
					function (serverMap) {
						that.serverMap = serverMap;
						sessionStorage.setItem('serverMap', JSON.stringify(serverMap));
						bus.$emit('serverMap');
					},
					function (code, msg) {
						console.log(code + "-" + msg);
					}
				);
			} else {
				that.serverMap = JSON.parse(sessionStorage.getItem('serverMap'));;
			}
		},

		getSoftwareProducts(serApiAddr) {
			let that = this;
			var urlAddr = that.serverMap.cadsvrplatserver;
			var tokenid = serApiAddr.tokenid;
			urlAddr += "/api/CADSvrPlat";
			urlAddr += '/Common/GetFirstSoftwareProducts/' + tokenid;
			that.$http.get(urlAddr).then(
				function (res) { // 响应成功回调
					if (undefined != res.data.DataContext && Object.prototype.toString.call(res.data.DataContext) == '[object Array]') {
						sessionStorage.setItem('softwareProducts', JSON.stringify(res.data.DataContext));
						// console.log("模块模板数据填充完毕");
						// bus.$emit('template', '1');
					}
				},
				function (res) {
					console.log(res);
				}
			);
		},

		getModuleTemplate() {
			let that = this;
			getAuth(
				function (serApiAddr) {
					var urlPlat = that.serverMap.cadsvrplatserver;
					var urlAddr = undefined;
					var tokenid_ = undefined;
					if (false) {
					//if (!serApiAddr.islogin) {
						var devAppIdSecret = JSON.parse(sessionStorage.getItem("serverMap")).DevAppIdSecret;
						urlAddr = that.serverMap.leadingapiserver + "/api/authorize/getappaccesstoken/" + devAppIdSecret;
						that.$http.get(urlAddr).then(
							function (res) {
								if (undefined != res.data.DataContext) {
									tokenid_ = res.data.DataContext.access_token;
									urlPlat += "/devapi/CADSvrPlat";
									urlAddr = urlPlat + '/Form/GetFormConfig/0/123456/' + tokenid_; //123456为userid，暂时没校验，随便给个值
									that.$http.get(urlAddr).then(
										function (res) { // 响应成功回调
											if (undefined != res.data.DataContext && undefined != res.data.DataContext.Config) {
												that.Items = that.genNavMenuData(JSON.parse(res.data.DataContext.Config));
												that.moduleParamsTemplate = that.genModuleParamsTemplate(JSON.parse(res.data.DataContext.Config));
												sessionStorage.setItem('navMenu', JSON.stringify(that.Items));
												sessionStorage.setItem('moduleParamsTemplate', JSON.stringify(that.moduleParamsTemplate));
												bus.$emit('template', '1');
											}
										},
										function (res) {
											console.log(res);
										}
									);
								}
							},
							function (res) {
								console.log(res);
							}
						);
					} else {
						tokenid_ = serApiAddr.tokenid;
						urlPlat += "/api/CADSvrPlat";
						urlAddr = urlPlat + '/Form/GetFormConfig/0/' + tokenid_;
						that.$http.get(urlAddr).then(
							function (res) { // 响应成功回调
								if (undefined != res.data.DataContext && undefined != res.data.DataContext.Config) {
									that.Items = that.genNavMenuData(JSON.parse(res.data.DataContext.Config));
									that.moduleParamsTemplate = that.genModuleParamsTemplate(JSON.parse(res.data.DataContext.Config));
									sessionStorage.setItem('navMenu', JSON.stringify(that.Items));
									sessionStorage.setItem('moduleParamsTemplate', JSON.stringify(that.moduleParamsTemplate));
									// console.log("模块模板数据填充完毕");
									bus.$emit('template', '1');
								}
							},
							function (res) {
								console.log(res);
							}
						);
					}
				},
				function (code, msg) {
					console.log(code + "-" + msg);
				}
			);
		},

		getModuleTreeFormModuleTemplate(moduleTemplate) {
			var userCfg = JSON.parse(sessionStorage.getItem("userCfg"));
			var appData = JSON.parse(sessionStorage.getItem("serverMap")).appdata;
			var moduleTree = new Object();
			moduleTree.Name = "全部软件";
			moduleTree.icon = "el-icon-menu";
			moduleTree.href = "/software/all";
			moduleTree.Items = [];
			if (undefined != moduleTemplate.controls && moduleTemplate.controls.length > 0) {
				for (var i = 0; i < moduleTemplate.controls.length; ++i) {
					if (undefined != moduleTemplate.controls[i].codenumber && "ClassificationList" == moduleTemplate.controls[i].codenumber) {
						if (undefined != moduleTemplate.controls[i].settings &&
							undefined != moduleTemplate.controls[i].settings.moptions &&
							moduleTemplate.controls[i].settings.moptions.length > 0) {
							for (var j = 0; j < moduleTemplate.controls[i].settings.moptions.length; ++j) {
								if (undefined != moduleTemplate.controls[i].settings.moptions[j].option &&
									moduleTemplate.controls[i].settings.moptions[j].option.length > 0) {
									for (var k = 0; k < moduleTemplate.controls[i].settings.moptions[j].option.length; ++k) {
										if (0 == j) {
											var treeOne = new Object();
											treeOne.Name = moduleTemplate.controls[i].settings.moptions[j].option[k].label;
											treeOne.key = moduleTemplate.controls[i].settings.moptions[j].option[k].key;
											treeOne.icon = "el-icon-school";
											treeOne.href = "/software";
											treeOne.Items = [];
											if ((undefined != appData.hidden && -1 != appData.hidden.indexOf(treeOne.key)) &&
												(undefined == userCfg.ShowModules || -1 == userCfg.ShowModules.indexOf(treeOne.key))) {
												continue;
											}
											moduleTree.Items.push(treeOne);
										} else if (1 == j) {
											for (var l = 0; l < moduleTree.Items.length; ++l) {
												if (moduleTree.Items[l].key == moduleTemplate.controls[i].settings.moptions[j].option[k].linkkey) {
													if (undefined != moduleTemplate.controls[i].settings.moptions[j].option[k].option &&
														moduleTemplate.controls[i].settings.moptions[j].option[k].option.length > 0) {
														for (var m = 0; m < moduleTemplate.controls[i].settings.moptions[j].option[k].option.length; ++m) {
															var treeTwo = new Object();
															treeTwo.Name = moduleTemplate.controls[i].settings.moptions[j].option[k].option[m].label;
															treeTwo.key = moduleTemplate.controls[i].settings.moptions[j].option[k].option[m].key;
															treeTwo.href = "/software";
															treeTwo.icon = "el-icon-school";
															treeTwo.Items = [];
															treeTwo.showItems = false;
															if ((undefined != appData.hidden && -1 != appData.hidden.indexOf(treeTwo.key)) &&
																(undefined == userCfg.ShowModules || -1 == userCfg.ShowModules.indexOf(treeTwo.key))) {
																continue;
															}
															moduleTree.Items[l].Items.push(treeTwo);
														}
													}
													break;
												}
											}
										} else if (2 == j) {
											for (var l = 0; l < moduleTree.Items.length; ++l) {
												if (undefined != moduleTree.Items[l].Items) {
													for (var m = 0; m < moduleTree.Items[l].Items.length; ++m) {
														if (moduleTree.Items[l].Items[m].key == moduleTemplate.controls[i].settings.moptions[j].option[k].linkkey) {
															if (undefined != moduleTemplate.controls[i].settings.moptions[j].option[k].option &&
																moduleTemplate.controls[i].settings.moptions[j].option[k].option.length > 0) {
																for (var n = 0; n < moduleTemplate.controls[i].settings.moptions[j].option[k].option.length; ++n) {
																	var treeThree = new Object();
																	treeThree.Name = moduleTemplate.controls[i].settings.moptions[j].option[k].option[n].label;
																	treeThree.key = moduleTemplate.controls[i].settings.moptions[j].option[k].option[n].key;
																	treeThree.href = "/software";
																	treeThree.icon = "el-icon-school";
																	treeThree.Items = [];
																	treeThree.showItems = false;
																	if ((undefined != appData.hidden && -1 != appData.hidden.indexOf(treeThree.key)) &&
																		(undefined == userCfg.ShowModules || -1 == userCfg.ShowModules.indexOf(treeThree.key))) {
																		continue;
																	}
																	moduleTree.Items[l].Items[m].Items.push(treeThree);
																}
															}
															break;
														}
													}
												}
											}
										} else if (3 == j) {
											for (var l = 0; l < moduleTree.Items.length; ++l) {
												if (undefined != moduleTree.Items[l].Items) {
													for (var m = 0; m < moduleTree.Items[l].Items.length; ++m) {
														if (undefined != moduleTree.Items[l].Items[m].Items) {
															for (var n = 0; n < moduleTree.Items[l].Items[m].Items.length; ++n) {
																if (moduleTree.Items[l].Items[m].Items[n].key == moduleTemplate.controls[i].settings.moptions[j].option[k].linkkey) {
																	if (undefined != moduleTemplate.controls[i].settings.moptions[j].option[k].option &&
																		moduleTemplate.controls[i].settings.moptions[j].option[k].option.length > 0) {
																		for (var o = 0; o < moduleTemplate.controls[i].settings.moptions[j].option[k].option.length; ++o) {
																			var treeFour = new Object();
																			treeFour.Name = moduleTemplate.controls[i].settings.moptions[j].option[k].option[o].label;
																			treeFour.key = moduleTemplate.controls[i].settings.moptions[j].option[k].option[o].key;
																			treeFour.href = "/software";
																			treeFour.icon = "el-icon-school";
																			treeFour.Items = [];
																			treeFour.showItems = false;
																			if ((undefined != appData.hidden && -1 != appData.hidden.indexOf(treeFour.key)) &&
																				(undefined == userCfg.ShowModules || -1 == userCfg.ShowModules.indexOf(treeFour.key))) {
																				continue;
																			}
																			moduleTree.Items[l].Items[m].Items[n].Items.push(treeFour);
																		}
																	}
																	break;
																}
															}
														}
													}
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}

			return moduleTree;
		},

		//生成左侧导航菜单数据（注意：有Items子节点的节点必须写href路由，否则默认展开菜单无效。）
		genNavMenuData(moduleTemplate) {
			var date = Date.now();
			var modelTree = this.getModuleTreeFormModuleTemplate(moduleTemplate);
			var navMenu = [{
				Name: "精品推荐",
				icon: "el-icon-news",
				href: "/reMen",
				key: (++date).toString(),
			},
			{
				Name: "我的",
				icon: "el-icon-user-solid",
				href: "/software/mine",
				key: (++date).toString(),
				Items: [{
					Name: "已购买",
					icon: "el-icon-goods",
					href: "/software/purchased",
					key: (++date).toString(),
				},
				// {
				// 	Name: "常用",
				// 	icon: "el-icon-tickets",
				// 	href: "/software/commonlyused",
				// 	key: (++date).toString(),
				// },
				// {
				// 	Name: "最近",
				// 	icon: "el-icon-edit-outline",
				// 	href: "/software/lately",
				// 	key: (++date).toString(),
				// },
				{
					Name: "收藏",
					icon: "el-icon-fa fa-heart",
					href: "/software/collection",
					key: (++date).toString(),
				}
				]
			},
			{
				Name: "操作指引",
				icon: "el-icon-chat-dot-square",
				href: "/quickStartTutorial",
				key: (++date).toString(),
			}
			];
			modelTree.key = (++date).toString();

			//增加处理非登录时，菜单显示项
			if (this.isLogin) {
				var arr = new Array();
				navMenu.forEach(ele => {
					arr.push(ele);
				})
				navMenu[0] = arr[1];
				navMenu[1] = modelTree;
				navMenu[2] = arr[0];
				if (this.showQuickStartTutorial) {
					navMenu[3] = arr[2];
				}
			} else {
				navMenu[1] = modelTree;
				if (!this.showQuickStartTutorial) {
					navMenu.pop();
				}
			}
			return navMenu;
		},

		genModuleParamsTemplate(moduleTemplate) {
			var moduleParamsTemplate = new Object();
			if (undefined != moduleTemplate.controls && moduleTemplate.controls.length > 0) {
				for (var i = 0; i < moduleTemplate.controls.length; ++i) {
					if (undefined != moduleTemplate.controls[i].codenumber) {
						var Obj = new Object();
						Obj.codenumber = moduleTemplate.controls[i].codenumber;
						Obj.fieldid = moduleTemplate.controls[i].fieldid;
						Obj.title = moduleTemplate.controls[i].title;
						moduleParamsTemplate[moduleTemplate.controls[i].codenumber] = Obj;

					}
				}
			}
			return moduleParamsTemplate;
		}
	}
}
