import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
import { types } from '../script/mutationTypes'

// 在router文件加下index.js或者router.js中添加如下代码
// 解决ElementUI导航栏中的vue-router在3.0版本以上重复点菜单报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

export const constantRoutes = [
	{
		path: '/reMen',
		component: () => import(/* webpackChunkName: "reMen" */'../components/reMen'),
		meta: {
			keepAlive: false
		}
	},
	{
		path: '/software-*',
		component: () => import(/* webpackChunkName: "softwarePage" */'../components/softwarePage'),
		meta: {
			keepAlive: false
		}
	},
	{
		path: "/software/:label",
		component: () => import(/* webpackChunkName: "softwarePage" */'../components/softwarePage'),
		meta: {
			keepAlive: false
		}
	},
	{
		path: '/',
		redirect: 'reMen',
		meta: {
			keepAlive: false
		}
	},
	{
		path: '/quickStartTutorial',
		component: () => import(/* webpackChunkName: "quickStartTutorial" */'../components/quickStartTutorial'),
		meta: {
			keepAlive: false
		}
	}
]

const createRouter = () => new VueRouter({
	// mode: 'history', // require service support
	scrollBehavior: () => ({
		y: 0
	}),
	routes: constantRoutes
})

const router = createRouter()
router.beforeEach((to, from , next) => {
	store.commit(types.CLEAR_PENDING);
	store.commit(types.CURRENT_ROUTE, { fullPath : to.fullPath })
	next()
})

export function resetRouter() {
	const newRouter = createRouter()
	router.matcher = newRouter.matcher // reset router
}

export default router
