import Vue from 'vue'
import Vuex from 'vuex'
import { types } from '../script/mutationTypes'
import { Loading } from 'element-ui'
import axios from 'axios'
import { softwarePageIntro } from "../utils/intro.js";

Vue.use(Vuex)
let loadingInstance;
let store = new Vuex.Store({
    state: {//类似存储全局变量的数据
        keepAlive: false, //是否使用页面缓存
        loginChange: false, //登录状态是否改变
        timeout: 8000, //超时延时 单位：毫秒
        timer: null, //定时器
        curRoutePath: '',
        httpRequestList: new Map()
    },
    getters: { //提供用来获取state数据的方法

    },
    mutations: {//提供存储设置state数据的方法
        [types.ALIVE](state, payload) {
            state.keepAlive = payload.isAlive;
        },
        [types.LOGIN](state, payload) {
            state.loginChange = payload.isChange;
        },
        [types.CURRENT_ROUTE](state, payload) {
            state.curRoutePath = payload.fullPath;
        },
        //添加请求
        [types.ADD_PENDING](state, config) {
            const url = [config.method, config.url, config.params, config.data].join('&')
            config.cancelToken = new axios.CancelToken((cancel) => {
                if (!state.httpRequestList.has(url)) {
                    //如果map中不存在当前请求，则加入
                    state.httpRequestList.set(url, cancel);
                }
            })
        },
        //移除请求
        [types.REMOVE_PENDING](state, config) {
            const url = [config.method, config.url, config.params, config.data].join('&')
            if (state.httpRequestList.has(url)) {
                //如果map中存在当前请求标识，则移除
                var cancel = state.httpRequestList.get(url)
                cancel(url)
                state.httpRequestList.delete(url)
            }
        },
        //清空并取消map中所有的请求
        [types.CLEAR_PENDING](state) {
            for (const [url, cancel] of state.httpRequestList) {
                cancel(url)
            }
            state.httpRequestList.clear()
        },
        //清空但不取消map中所有请求
        [types.EMPTY_PENDING](state) {
            state.httpRequestList.clear()
        }

    },
    actions: { //提供跟后台接口打交道的方法，并调用mutations提供的方法
        //登录状态变化的请求(含超时提示)
        loading({ commit, state }, payload) {
            var loadingConfig = {
                lock: true,
                text: '加载中...',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.3)',
                customClass: 'create-isLoading'
            };
            commit(types.LOGIN, payload);
            commit(types.CLEAR_PENDING);
            if (state.loginChange) {
                if (state.timer) {
                    clearTimeout(state.timer);
                }
                loadingInstance = Loading.service(loadingConfig);

                state.timer = setTimeout(() => {
                    loadingConfig.text = "请求超时！";
                    loadingConfig.spinner = "el-icon-warning";
                    loadingInstance.close();
                    loadingInstance = Loading.service(loadingConfig);
                    setTimeout(() => {
                        loadingInstance.close();
                    }, 1500)
                }, state.timeout)
            } else {
                if (state.timer) {
                    clearTimeout(state.timer);
                }
                setTimeout(() => {
                    loadingInstance.close();
                }, 20)
            }
        },

        // 引导信息变化的请求
        setIntro({ commit, state }, payload) {
            
        }

    },
    modules: {

    }
})

export default store