import axios from 'axios'
import store from '../store'
import { types } from './mutationTypes'

//添加请求拦截器
const interceptorRequestInstance = axios.interceptors.request.use((config) => {
    if (config && config.url.indexOf('CADSoftwareMall/Authorization/CheckAuthorization/') != -1) {
        store.commit(types.REMOVE_PENDING, config);
        store.commit(types.ADD_PENDING, config);
    }

    return config;
}, (error) => {
    if (axios.isCancel(error)) {
        // console.log('canceled request: ' , error.message)
    } else {//其他错误
        console.log(error.message)
    }
    return Promise.reject(error);
})

// 添加响应拦截器
const interceptorResponseInstance = axios.interceptors.response.use(function (response) {
    store.commit(types.REMOVE_PENDING, response.config);
    return response;
}, function (error) {
    if (axios.isCancel(error)) {

    } else {
        console.log(error.message)
    }
    return Promise.reject(error);
})
